<template>
  <el-input v-model="pageInfo.queryId"  style="width:250px ;height:40px" placeholder="(2020022)" clearable="true"/>
  <el-button  @click="getAll" style="height:38px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>

  <el-button type="success" @click="add" plain></el-button>
  <br><br>


  <el-dialog v-model="dialogFormVisible" title="" width="30%">
    <el-form :model="Student" >
      <el-form-item label="id" :label-width="formLabelWidth" >
        <el-input v-model="Student.s_id" placeholder="id" autocomplete="off" />
      </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="Student.s_name" placeholder="" autocomplete="off" />
        </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select v-model="Student.s_sex" placeholder="Please select">
          <el-option label="" value="" />
          <el-option label="" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_password" placeholder="" autocomplete="off" />
      </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="Student.s_department"  placeholder="" autocomplete="off" />
        </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_major" placeholder="" autocomplete="off" />
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_class" placeholder="" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel"></el-button>
        <el-button type="success" @click="handleAdd"></el-button>
      </span>
    </template>
  </el-dialog>


  <el-dialog v-model="dialogFormVisibleEdit" title="" width="30%">
    <el-form :model="Student" >
      <el-form-item label="id" :label-width="formLabelWidth">
        <el-input v-model="Student.s_id" placeholder="id" autocomplete="off" disabled="true"/>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_name" placeholder="" autocomplete="off" />
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select v-model="Student.s_sex" placeholder="Please select">
          <el-option label="" value="" />
          <el-option label="" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_password" placeholder="" autocomplete="off" />
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_department"  placeholder="" autocomplete="off" />
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_major" placeholder="" autocomplete="off" />
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Student.s_class" placeholder="" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel"></el-button>
        <el-button type="success" @click="handleUpdate"></el-button>
      </span>
    </template>
  </el-dialog>


  <el-table :data="tableData" stripe style="width: 100%">
    <el-table-column prop="s_id" label="id" align="center"/>
    <el-table-column prop="s_name" label="" align="center"/>
    <el-table-column prop="s_sex" label="" align="center"/>
    <el-table-column prop="s_password" label="" align="center"/>
    <el-table-column prop="s_department" label="" width="250px" align="center"/>
    <el-table-column prop="s_major" label="" align="center"/>
    <el-table-column prop="s_class" label="" align="center"/>

    <el-table-column   label="" align="center">

      <template #default="scope">
        <el-button size="small" type="info" @click="handleEdit(scope.row)"></el-button>
        <el-button size="small" type="danger" @click="handleDelete(scope.row)"></el-button>
      </template>

    </el-table-column>
  </el-table>

<div style="text-align: -webkit-center">
  <el-pagination
      v-model:currentPage="pageInfo.currentPage"
      v-model:page-size="pageInfo.pageSize"
      :page-sizes="[6, 8, 10]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageInfo.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  />
</div>

</template>

<script>
import axios from 'axios'
export default {
  name: "ManStudent",
  data(){
    return{
      dialogFormVisible:false,
      dialogFormVisibleEdit:false,
      Student:{},
      formLabelWidth:'140px',
      tableData:[],

      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total:0,
        queryId:''
      },
    }
  },

  created(){
    this.getAll()
  },


  methods:{

    cancel(){
      this.dialogFormVisible=false
      this.dialogFormVisibleEdit=false
      this.$message.info("")
    },

    getAll(){

      let param;
      param="?currentPage="+this.pageInfo.currentPage
      param +="&pageSize="+this.pageInfo.pageSize
      param +="&s_id="+this.pageInfo.queryId


      axios.get("/managers/Stu/"+param).then((res)=>
      {
        console.log(res)
        this.pageInfo.currentPage=res.data.data.current
        this.pageInfo.pageSize=res.data.data.size
        this.pageInfo.total=res.data.data.total

        this.tableData =res.data.data.records

      })
    },
    resetForm(){
     this.Student={}
    },
//
    add(){
      this.dialogFormVisible = true
      this.resetForm()
    },
//
    handleAdd(){
      if(this.Student.s_id==null||this.Student.s_name==null||this.Student.s_sex==null||this.Student.s_password==null||this.Student.s_department==null ||
          this.Student.s_major==null||this.Student.s_class==null)
        this.$message.error("!")

      else
      {axios.post("/managers/saveStu",this.Student).then(res=>{
            console.log(res)
            if(res.data.flag)
            { this.dialogFormVisible=false;
              this.$message.success("")
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    }},

//
    handleEdit(row){
      axios.get("/managers/getOneStu/?sid="+row.s_id).then((res)=>{
        if(res.data.flag){
          this.Student=res.data.data
          this.dialogFormVisibleEdit=true;
        }
        else{
          this.$message.error("");
        }
      }).finally(()=>{
        this.getAll();
      })
    },

//
    handleUpdate(){
      axios.put("/managers/updateStu",this.Student).then(res=>{
            console.log(res)
            if(res.data.flag) {
              this.$message.success("")
              this.dialogFormVisibleEdit = false
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    },

    handleDelete(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios(
            {
              url:"/managers/deleteStu",
              method:"DELETE",
              data:{s_id:row.s_id}
            }
        ).then((res) => {
              if (res.data.flag)
                this.$message.success('!');
              else
                this.$message.error(res.data.msg);
            }
        ).finally(() => {
              this.getAll()
            }
        )
      }).catch(() => {
        this.$message.info('');
      })
    },


      handleSizeChange(pageSize) {
      this.pageInfo.pageSize=pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage=currentPage
      this.getAll()
    }

  }
}

</script>

<style scoped>
.el-pagination{
  width: max-content;
  margin-top:20px;
}
</style>