<template>
  <div :style="background" class="bgBackground">
  <div className="center">
    <br><br> <br><br>
    <h1></h1>
    <br><br>
    <el-input
        v-model="formLabelAlign.username"
        placeholder="userID"
        style="width:300px; height:40px"
        clearable="true"

    />
    <br><br>
    <el-input
        v-model="formLabelAlign.password"
        type="password"
        style="width:300px; height:40px"
        placeholder="password"
        show-password
        clearable="true"
    />
    <br><br> <br><br>



    <el-dropdown>
    <span class="el-dropdown-link">
      
      <el-icon class="el-icon--right">
        <arrow-down />
      </el-icon>
    </span>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item @click="StuLogin" style="font-size:20px"></el-dropdown-item>
          <el-dropdown-item @click="TeaLogin" style="font-size:20px"></el-dropdown-item>
          <el-dropdown-item @click="ManLogin" style="font-size:20px"></el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </div>

  </div>
</template>


<script>
import axios from 'axios'
export default {
  name: "LoGin",
  data() {
    return {
      formLabelAlign:
          {
            username: "",
            password: "",
            name:""
          },

      background: {
        // 
        backgroundImage: 'url(' + require('@/assets/blue.jpg') + ')',
        // 
        backgroundRepeat: 'no-repeat',
        // 
        backgroundSize: 'cover',
        // 
        backgroundColor: '#000',
        // 
        backgroundPosition: 'center top'
      }

    }
  },
  methods: {

    StuLogin(){
      if(this.formLabelAlign.username==null||this.formLabelAlign.password==null) this.$message.info("")
      else{
        axios.post("/students/login"+"?username="+this.formLabelAlign.username+"&password="+this.formLabelAlign.password).then((res)=> {
          if (res.data.flag) {
            console.log(res.data)
            this.formLabelAlign.name=res.data.data
            sessionStorage.setItem("student", this.formLabelAlign.username)
            sessionStorage.setItem("stuName", this.formLabelAlign.name)
            this.$alert("  " + sessionStorage.getItem("stuName") + "", "", {
              confirmButtonText: ''
            })
            this.$router.push('/stu-login');
          }

          else {
            console.log(res.data)
            this.$alert(res.data.msg, "", {
              confirmButtonText: ''
            })
          }
        })
      }

    },

    TeaLogin(){
      if(this.formLabelAlign.username==null||this.formLabelAlign.password==null) this.$message.info("")
      else{
        axios.post("/teachers/login"+"?username="+this.formLabelAlign.username+"&password="+this.formLabelAlign.password).then((res)=> {
          if (res.data.flag) {
            console.log(res.data)
            this.formLabelAlign.name=res.data.data
            sessionStorage.setItem("teaName", this.formLabelAlign.name)
            sessionStorage.setItem("teacher", this.formLabelAlign.username)
            this.$alert("  " + sessionStorage.getItem("teaName") + "", "", {
              confirmButtonText: ''
            })
            this.$router.push('/tea-login');
          }
          else {
            console.log(res.data)
            this.$alert(res.data.msg, "", {
              confirmButtonText: ''
            })
          }
        })
      }

    },

    ManLogin(){
      if(this.formLabelAlign.username==null||this.formLabelAlign.password==null) this.$message.info("")
      else{
        axios.post("/managers/login"+"?username="+this.formLabelAlign.username+"&password="+this.formLabelAlign.password).then((res)=> {
          if (res.data.flag) {
            console.log(res.data)
            this.formLabelAlign.name=res.data.data
            sessionStorage.setItem("manName", this.formLabelAlign.name)
            this.$alert("  " + sessionStorage.getItem("manName") + "", "", {
              confirmButtonText: ''
            })
            this.$router.push('/man-login');
          }

          else {
            console.log(res.data)
            this.$alert(res.data.msg, "", {
              confirmButtonText: ''
            })
          }
        })
      }

    }

  }
}
</script>


<style scoped>
.center {
  text-align: center;
}

.example-showcase {

  cursor: pointer;
  color: #409EFF;
  display: flex;
  align-items: center;

}

.el-dropdown-link {
  font-size:25px;
  cursor: pointer;
  color: #07fa07;
  display: flex;
  align-items: center;
}

.bgBackground{
  width:100%;
  height:765px;
}

</style>

