<template>
  <el-input v-model="pageInfo.queryId"  style="width:250px ;height:40px" placeholder="id2010011)" clearable="true"/>
  <el-button  @click="getAll" style="height:38px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>

  <el-button type="success" @click="add" plain></el-button>
  <br><br>
  <el-dialog v-model="dialogFormVisible" title="" width="30%">
    <el-form :model="Teacher">
      <el-form-item label="id" :label-width="formLabelWidth" >
        <el-input v-model="Teacher.t_id" placeholder="id" autocomplete="off" />
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_name" placeholder="" autocomplete="off" />
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-select v-model="Teacher.t_sex" placeholder="Please select">
          <el-option label="" value="" />
          <el-option label="" value="" />
        </el-select>
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_password"  placeholder="" autocomplete="off" />
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.telephone"  placeholder="" autocomplete="off" />
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_department" placeholder="" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel"></el-button>
        <el-button type="success" @click="handleAdd"></el-button>
      </span>
    </template>
  </el-dialog>

  <el-dialog v-model="dialogFormVisibleEdit" title="" width="30%">
    <el-form :model="Teacher">
      <el-form-item label="id" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_id" placeholder="id" autocomplete="off" disabled="true"/>
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_name" placeholder="" autocomplete="off" />
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-select v-model="Teacher.t_sex" placeholder="Please select">
          <el-option label="" value="" />
          <el-option label="" value="" />
        </el-select>
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_password"  placeholder="" autocomplete="off" />
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.telephone"  placeholder="" autocomplete="off" />
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_department" placeholder="" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel"></el-button>
        <el-button type="success" @click="handleUpdate"></el-button>
      </span>
    </template>
  </el-dialog>

  <el-table :data="tableData" stripe style="width: 100%">
    <el-table-column prop="t_id" label="id"  align="center"/>
    <el-table-column prop="t_name" label=""  align="center" />
    <el-table-column prop="t_sex" label="" align="center"/>
    <el-table-column prop="t_password" label="" align="center"/>
    <el-table-column prop="t_department" label="" align="center" width="250px"/>
    <el-table-column prop="telephone" label="" align="center"/>
    <el-table-column label="" align="center">

      <template #default="scope">
        <el-button size="small" type="info" @click="handleEdit(scope.row)"></el-button>
        <el-button size="small" type="danger" @click="handleDelete(scope.row)"></el-button>
      </template>

    </el-table-column >
  </el-table>

  <div style="text-align: -webkit-center">
  <el-pagination
      v-model:currentPage="pageInfo.currentPage"
      v-model:page-size="pageInfo.pageSize"
      :page-sizes="[6, 8, 10]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageInfo.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  />
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "ManTeacher",
  data(){
    return{
      dialogFormVisible:false,
      dialogFormVisibleEdit:false,
      Teacher:{},
      formLabelWidth:'140px',
      tableData:[],

      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total:0,
        queryId:''
      },
    }
  },

  created(){
    this.getAll()
  },

  methods:{
    cancel(){
      this.dialogFormVisible=false
      this.dialogFormVisibleEdit=false
      this.$message.info("")
    },

    getAll(){
      let param;
      param="?currentPage="+this.pageInfo.currentPage
      param +="&pageSize="+this.pageInfo.pageSize
      param +="&t_id="+this.pageInfo.queryId

      axios.get("/managers/Tea/"+param).then((res)=>
      {
        console.log(res)
        this.pageInfo.currentPage=res.data.data.current
        this.pageInfo.pageSize=res.data.data.size
        this.pageInfo.total=res.data.data.total

        this.tableData =res.data.data.records

      })
    },
    resetForm(){
      this.Teacher={}
    },
//
    add(){
      this.dialogFormVisible = true
      this.resetForm()
    },
//
    handleAdd(){
      if(this.Teacher.t_id==null||this.Teacher.t_name==null||this.Teacher.t_sex==null||this.Teacher.t_password==null
          ||this.Teacher.t_department==null || this.Teacher.telephone==null)
        this.$message.error("!")
      else
      {axios.post("/managers/saveTea",this.Teacher).then(res=>{
            console.log(res)
            if(res.data.flag)
            { this.dialogFormVisible=false;
              this.$message.success("")
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    }},

//
    handleEdit(row){
      axios.get("/managers/getOneTea/?tid="+row.t_id).then((res)=>{
        if(res.data.flag){
          this.Teacher=res.data.data
          this.dialogFormVisibleEdit=true;
        }
        else{
          this.$message.error(res.data.msg);
        }
      }).finally(()=>{
        this.getAll();
      })
    },

//
    handleUpdate(){
      axios.put("/managers/updateTea",this.Teacher).then(res=>{
            console.log(res)
            if(res.data.flag) {
              this.$message.success("")
              this.dialogFormVisibleEdit = false
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    },

    handleDelete(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios(
            {
              url:"/managers/deleteTea",
              method:"DELETE",
              data:{t_id:row.t_id}
            }
        ).then((res) => {
              if (res.data.flag)
                this.$message.success('!');
              else
                this.$message.error(res.data.msg);
            }
        ).finally(() => {
              this.getAll()
            }
        )
      }).catch(() => {
        this.$message.info('');
      })
    },


    handleSizeChange(pageSize) {
      this.pageInfo.pageSize=pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage=currentPage
      this.getAll()
    }

  }
}

</script>

<style scoped>
.el-pagination{
  width: max-content;
  margin-top:20px;
}
</style>